.placeHolderWhite::placeholder{
  color:white !important;
}

.cursorPointer{
  cursor: pointer !important;
}

.form-switch .form-check-input {
  border: 1px solid rgba(52, 125, 250, 0.511);
  --bs-form-switch-bg: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%2386b7fe'/%3e%3c/svg%3e")
}

.loadingBarBox{
  height: 3px;
  width: 100%;
}

.loadingBar{
  z-index: 1;
  height: 3px;
  background-color: rgb(220, 0, 0);
  width: 80% ;
  animation: loading 2.2s;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.completeLoading{
  z-index: 1;
  height: 0px;
  background-color: rgb(220, 0, 0);
  width: 100%;
  animation: loadingCompleted 0.5s !important;
}

@keyframes loading {
  0%{
      width: 0%;
  }
  15%{
      width: 25%;
  }
  30%{
      width: 33%;
  }
  50%{
      width:40%;
  }
  75%{
      width: 50%;
  }
  80%{
      width: 51%;
  }
  82%{
      width: 52%;
  }
  84%{
      width: 54%;
  }
  86%{
      width: 56%;
  }
  88%{
      width: 58%;
  }
  90%{
      width: 60%;
  }
  92%{
      width: 63%;
  }
  94%{
      width: 66%;
  }
  96%{
      width: 69%;
  }
  98%{
      width: 72%;
  }
  100%{
      width: 75%;
  }
}

@keyframes loadingCompleted {
  0%{
      width: 80%;
      height: 3px;
  }
  30%{
      width: 90%;
  }
  70%{
      width: 95%;
      height: 3px;
  }
  100%{
      width: 100%;
      height: 0px;
  }
}

.alert{
  position: fixed !important;
  width: 300px !important;
  z-index: 100 !important;
  animation: slide 0.2s ease-in-out;
}

@keyframes slide {
  0%{
    transform: translateX(-300px);
  }
  100%{
    transform: translateX(0);
  }
}

.loadBar{
  width: 0%;
  height: 5px;
  animation: loadBarAnimation 1.5s;
  border-radius: 1px;
}

@keyframes loadBarAnimation {
  0%{
    width: 100%;
  }
  100%{
    width: 0%;
  }
}

.bg-darkGreen{
  background-color: darkgreen;
}

.bg-darkRed{
  background-color: darkred;
}

.input-manual-style{
  width: 300px !important;
}

.btn-large{
  width: 100%;
}

.dropdown-toggle{
  border: none !important;
}

.cursorPointer{
  cursor: pointer !important;
}